        font-weight: 500;
        font-size: 14px;
        color: #0B6FF9;
        font-style: normal;
        text-align: center;
    }

    #swiper {
        height: 400px;
    }

    #bigData {
        padding: 60px;
    }

    #bigData .bigData-title {
        font-size: 30px;
    }

    #bigData .bigData-device {
        font-size: 30px;
        margin: 30px 0 15px;
    }

    #contactUs {
        width: 100%;
        text-align: center;
        height: 300px;
    }

    #contactUs .contactUs-container {
        padding-top: 50px;
    }

    #contactUs .contactUs-container h1 {
        font-size: 30px;
    }

    #contactUs .contactUs-container h3 {
        font-size: 20px;
    }

    #contactUs .contactUs-container button {
        width: 300px;
        height: 50px;
        margin-top: 30px;
    }

    #contactUs .contactUs-container .contactUs-contactWay span {
        display: inline-block;
        width: 32px;
        height: 32px;
        margin: 15px;
    }

    #customer .customer-title {
        font-size: 24px;
    }

    #whyChooseUs {
        padding: 20px 0;
    }
}
</style>


